<template>
  <div
    class="_singlePage"
    :class="{
      'is--preview': context === 'list',
      'is--editable': can_edit,
    }"
  >
    <div
      class="_container"
      :style="page_styles"
      @click.self="active_module = false"
    >
      <div class="_content" @click.self="active_module = false">
        <svg
          v-if="can_edit && gridstep"
          class="_grid"
          width="100%"
          height="100%"
          xmlns="http://www.w3.org/2000/svg"
        >
          <defs>
            <pattern
              id="gridSmall"
              :width="gridstep"
              :height="gridstep"
              patternUnits="userSpaceOnUse"
            >
              <path
                :d="`M ${gridstep} 0 L 0 0 0 ${gridstep}`"
                fill="none"
                stroke="rgba(207, 207, 207, 0.5)"
                strokeWidth="1"
              />
            </pattern>
            <pattern
              id="grid"
              :width="gridstep * 5"
              :height="gridstep * 5"
              patternUnits="userSpaceOnUse"
            >
              <rect
                :width="gridstep * 5"
                :height="gridstep * 5"
                fill="url(#gridSmall)"
              ></rect>
              <path
                :d="`M ${gridstep * 5} 0 L 0 0 0 ${gridstep * 5}`"
                fill="none"
                stroke="rgba(186, 186, 186, 0.5)"
                strokeWidth="1"
              />
            </pattern>
          </defs>
          <rect width="100%" height="100%" fill="url(#grid)"></rect>
        </svg>
        <MoveableItem
          class="_item"
          v-for="publimodule in page_modules"
          :key="publimodule.$path"
          :publimodule="publimodule"
          :magnification="magnification"
          :gridstep="gridstep"
          :zoom="zoom"
          :can_edit="can_edit"
          :is_active.sync="active_module"
        />

        <svg
          v-if="can_edit && margins"
          class="_margins"
          width="100%"
          height="100%"
          xmlns="http://www.w3.org/2000/svg"
        >
          <!-- top -->
          <line
            :x1="transformMargins(margins.left)"
            :y1="transformMargins(margins.top)"
            :x2="transformMargins(page_width - margins.right)"
            :y2="transformMargins(margins.top)"
            stroke="rebeccapurple"
          />
          <!-- bottom -->
          <line
            :x1="transformMargins(margins.left)"
            :y1="transformMargins(page_height - margins.bottom)"
            :x2="transformMargins(page_width - margins.right)"
            :y2="transformMargins(page_height - margins.bottom)"
            stroke="rebeccapurple"
          />
          <!-- left -->
          <line
            :x1="transformMargins(margins.left)"
            :y1="transformMargins(margins.top)"
            :x2="transformMargins(margins.left)"
            :y2="transformMargins(page_height - margins.bottom)"
            stroke="rebeccapurple"
          />
          <!-- right -->
          <line
            :x1="transformMargins(page_width - margins.right)"
            :y1="transformMargins(margins.top)"
            :x2="transformMargins(page_width - margins.right)"
            :y2="transformMargins(page_height - margins.bottom)"
            stroke="rebeccapurple"
          />
        </svg>
      </div>
    </div>
  </div>
</template>
<script>
import MoveableItem from "@/components/publications/page_by_page/MoveableItem.vue";

export default {
  props: {
    context: String,
    publication_path: String,
    page_modules: Array,
    page_width: Number,
    page_height: Number,
    zoom: { type: Number, default: 1 },
    gridstep_in_cm: Number,
    margins: Object,
    magnification: { type: Number, default: 38 },
    can_edit: Boolean,
  },
  components: {
    MoveableItem,
  },
  data() {
    return {
      items: [{ src: "images/i_add_publi.svg" }, { src: "images/i_add.svg" }],
      active_module: false,
    };
  },
  created() {},
  mounted() {},
  beforeDestroy() {},
  watch: {},
  computed: {
    gridstep() {
      if (!this.gridstep_in_cm) return 0;
      return this.gridstep_in_cm * this.magnification;
    },
    page_styles() {
      return `
        --page-width: ${this.page_width * this.magnification}px;
        --page-height: ${this.page_height * this.magnification}px;
        --zoom: ${this.zoom};
      `;
    },
  },
  methods: {
    transformMargins(m) {
      return m * this.magnification;
    },
    // async updateMeta({ new_meta }) {
    //   this.fetch_status = "pending";
    //   this.fetch_error = null;
    //   try {
    //     this.response = await this.$api.updateMeta({
    //       path: this.publication_path,
    //       new_meta,
    //     });
    //     this.fetch_status = "success";
    //   } catch (e) {
    //     this.fetch_status = "error";
    //     this.fetch_error = e.response.data;
    //   }
    // },
  },
};
</script>
<style lang="scss" scoped>
._singlePage {
  // padding: calc(var(--spacing) * 1);
}

._container {
  width: 100%;
  height: calc(var(--page-height) * var(--zoom));
  margin: calc(var(--spacing) * 2) auto;

  .is--preview & {
    width: calc(var(--page-width) * var(--zoom));
    margin: 0 auto;
  }
}

._content {
  display: block;
  position: relative;
  z-index: 0;

  margin: 0 auto;
  width: var(--page-width, 10cm);
  height: var(--page-height, 10cm);

  transform: scale(var(--zoom));
  transform-origin: 50% 25%;

  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.19, 1, 0.22, 1);

  background: white;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);

  ._singlePage.is--preview & {
    transform-origin: top left;
  }
  ._singlePage.is--editable & {
    overflow: visible;
  }
}

._item {
  // position: absolute;
}

._grid {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
._margins {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
</style>
